import nav from '@/components/nav/nav.vue';
import {mapState} from 'vuex';
import wx from 'weixin-jsapi';
export default{
    data() {
        return {
            storeCode: 'test1',
            wxConfig: {}
        };
    },
    computed: {
        ...mapState(['global', 'weixin'])
    },
    watch: {
    },
    methods: {
        settlement() {
            this.axios({
                method: 'post',
                url: '/Api/GuiBankPay',
                data: '{"id":19,"jsonrpc":"2.0","method":"getWeChatJsSdkData","params":{"openId":"' + this.weixin.openId + '"}}',
                contentType: 'application/json',
                dataType: 'json'
            }).then((result) => {
                console.log(this.wx);
                wx.config({
                    debug: false,
                    appId: result.data.result.appId,
                    timestamp: result.data.result.timestamp,
                    nonceStr: result.data.result.nonceStr,
                    signature: result.data.result.signature,
                    // 所有要调用的 API 都要加到这个列表中
                    // 要调用的微信接口
                    jsApiList: [
                        'checkJsApi',
                        'startRecord',
                        'stopRecord',
                        'translateVoice',
                        'scanQRCode',
                        'openCard'
                    ]
                });
                wx.ready(function() {
                    wx.scanQRCode({
                        needResult: 1, // 默认为0，扫描结果由微信处理，1则直接返回扫描结果，
                        scanType: ['qrCode', 'barCode'], // 可以指定扫二维码还是一维码，默认二者都有
                        success: function(res) {
                            console.log(res);
                            alert(res);
                        // var objdata= res.resultStr; // 当needResult 为 1 时，扫码返回的结果
                        // that.text_search = objdata.assetqrcode
                        // that.getAllTableData(that.text_search)//获取想要获取的数据，比如说table列表
                        }
                    });
                });
            });
            // this.$router.push({path: `/settlement/${this.storeCode}`});
        }

    },
    created () {
        console.log(wx);
    },
    components: {
        'v-nav': nav
    }
};



